<template>
  <div class="question-test">
    <template v-if="questionList.length">
      <h4 class="title">{{ $route.meta.title }}测试</h4>
      <div
        class="time"
        :class="this.$route.name === 'abilityTestResult' ? 'end-time' : ''"
      >
        <p
          class="second"
          :class="
            this.$route.name !== 'abilityTestResult' ? 'start-second' : ''
          "
        >
          考试用时：{{ getTime(startTime, endTime) }}分钟
        </p>
        <p v-if="this.$route.name === 'abilityTestResult'" class="score">
          分数：{{ score || 0 }}分
        </p>
        <p v-if="this.$route.name === 'abilityTestResult'" class="error">
          错题数：{{ errorNum.length }}个
        </p>
      </div>
      <p class="tip-sign">
        不定项选择题 (共{{ questionList.length }}道题，每题{{
          parseInt(100 / questionList.length) || 0
        }}分)
      </p>
      <div class="question-list">
        <div v-for="(item, idx) of questionList" :key="idx" class="item">
          <p class="stem">{{ idx + 1 }}.<span v-html="item.stem" /></p>
          <div v-if="item.img" class="image-list">
            <img v-for="imgUrl of item.img" :key="imgUrl" :src="imgUrl" />
          </div>
          <el-checkbox-group
            v-if="item.type === 'checkbox'"
            v-model="answers[idx]"
            size="small"
            :disabled="$route.name === 'abilityTestResult'"
          >
            <el-checkbox
              v-for="option of item.options"
              :key="option.index"
              :label="option.index"
              :class="getResultColor(option.index, idx)"
            >
              <svg
                v-if="getResultColor(option.index, idx) === 'error'"
                class="icon test-icon"
                aria-hidden="true"
              >
                <use xlink:href="#icon-btn-pic-closex-copy" />
              </svg>
              <svg
                v-if="getResultColor(option.index, idx) === 'success'"
                class="icon test-icon"
                aria-hidden="true"
              >
                <use xlink:href="#icon-ico-jkda-xzx" />
              </svg>
              <span>{{ option.index + 1 }}</span
              >. <span v-html="option.item" />
            </el-checkbox>
          </el-checkbox-group>
          <el-radio-group
            v-if="item.type === 'radio'"
            v-model="answers[idx]"
            size="small"
            :disabled="$route.name === 'abilityTestResult'"
          >
            <el-radio
              v-for="(option, index) of item.options"
              :key="index"
              :label="index"
              :class="getResultColor(index, idx)"
            >
              <svg
                v-if="getResultColor(option.index, idx) === 'error'"
                class="icon test-icon"
                aria-hidden="true"
              >
                <use xlink:href="#icon-btn-pic-closex-copy" />
              </svg>
              <svg
                v-if="getResultColor(option.index, idx) === 'success'"
                class="icon test-icon"
                aria-hidden="true"
              >
                <use xlink:href="#icon-ico-jkda-xzx" />
              </svg>
              <span>{{ index + 1 }}</span
              >. <span v-html="option.item" />
            </el-radio>
          </el-radio-group>
        </div>
      </div>
      <div class="commit-btn">
        <el-button
          v-if="$route.name === 'abilityTest'"
          type="primary"
          @click="commitQuestion"
          >提交</el-button
        >
        <!--<el-button v-if="$route.name === 'abilityTestResult'" @click="$router.push(`/study/test/${$route.params.id}`)">重新测试</el-button>-->
        <el-button
          v-if="$route.name === 'abilityTestResult'"
          @click="$router.push('/study')"
          >完成</el-button
        >
      </div>
    </template>
    <no-enterprise v-if="noTest" />
  </div>
</template>

<script>
import NoEnterprise from './NoEnterprise'
import { getDateInfo } from '@/utils/learningMethod'
