  components: {
    NoEnterprise
  },
  data() {
    return {
      questionList: [],
      minute: 0,
      timer: null,
      mtsUserRecord: {},
      questions: [],
      errorNum: [],
      answers: [],
      noTest: false,
      startTime: '',
      endTime: '',
      questionAnswer: [],
      score: 0,
      testAnswer: false
    }
  },
  watch: {
    '$route.name'(value) {
      if (value === 'abilityTestResult') {
        abilityTest({
          started_at: this.$store.state.study.startTime,
          ended_at: this.endTime || getDateInfo(new Date()),
          score: parseInt(this.score),
          lesson_column: this.getAbilityType(this.$route.params.name)
        })
      }
    }
  },
  computed: {
    getTime() {
      return function(start, end) {
        if (start && end) {
          let time = new Date(end).getTime() - new Date(start).getTime()
          time = time / 1000 / 60
          return Math.ceil(Math.abs(time))
        } else return this.minute
      }
    },
    getResultColor() {
      return function(index, i) {
        if (this.$route.name === 'abilityTestResult' && this.testAnswer) {
          for (const item of this.answers[i]) {
            if (item === index) return 'success'
          }
          if (this.questionAnswer.length > 0) {
            for (const item of this.questionAnswer[i]) {
              if (item === index) {
                // eslint-disable-next-line vue/no-side-effects-in-computed-properties
                if (this.errorNum.indexOf(i) === -1) this.errorNum.push(i)
                return 'error'
              }
            }
          }
          // eslint-disable-next-line vue/no-side-effects-in-computed-properties
          this.score = parseInt(
            (100 / this.questionList.length) *
              (this.questionList.length - this.errorNum.length)
          )
        } else return ''
      }
    },
    getAbilityType() {
      return function(str) {
        if (str === '用户问题确诊能力') return 'problem_diagnosis_ability'
