            { index: 1, item: '一般来说孩子六个月到6岁一年生病5-7次很常见' },
            { index: 2, item: '呼吸道感染恢复期一般需要2周' },
            {
              index: 3,
              item:
                '建议带孩子去医院检查一下免疫力，看是否是因为免疫力低下导致爱生病'
            }
          ],
          type: 'radio'
        },
        {
          stem:
            '宝宝8个月了，腹泻9天了，平均每天拉5-6次，去医院检查粪便，倒没啥特殊的情况，医生就说是消化不良，服用双歧杆菌、儿泄停、蒙脱石散效果甚微，每次排便量较少、味酸，颜色黄绿色，宝宝精神很好，偶尔腹胀，肚子会咕噜噜响，宝宝这种情况是怎么了?关于上述案例下列回答哪项正确',
          options: [
            { index: 0, item: '考虑胃肠功能紊乱' },
            { index: 1, item: '考虑宝宝消化系统不完善' },
            { index: 2, item: '考虑是继发乳糖不耐受' },
            { index: 3, item: '考虑腹泻导致' }
          ],
