            { index: 2, item: '是一种感染性疾病' },
            { index: 3, item: '宝宝会有瘙痒感' }
          ],
          type: 'checkbox',
          img: [imgOne, imgTwo]
        },
        {
          stem:
            '宝宝2岁5个月，两三天才拉一次，大便都比较硬，排便困难，有时候还会拉颗粒状大便，火龙果香蕉苹果也在吃，好像也没什么效果，未添加新食物，以下选项正确的是',
          options: [
            { index: 0, item: '考虑便秘的可能' },
