            '女宝宝，一岁9个月，宝宝身上起了很多红疙瘩，降低室内温度后有改善，请问这是什么？',
          options: [
            { index: 0, item: '湿疹' },
            { index: 1, item: '痱子' },
            { index: 2, item: '过敏' },
            { index: 3, item: '痤疮' }
          ],
          type: 'radio',
