  methods: {
    async commitQuestion() {
      this.$store.state.study.questionAnswer = this.answers
      for (const item of this.answers) {
        if ((item + '').length === 0)
          return this.$message({
            message: '请做完题目后再提交',
            type: 'error'
          })
      }
      this.endTime = getDateInfo(new Date())
      for (const item of this.$store.state.study.questionAnswer) {
