        this.answers = [
          [1],
          [2],
          [2],
          [1],
          [3],
          [0],
          [1],
          [2],
          [0, 1, 2],
          [0, 1, 2]
        ]
      }
      if (this.$route.params.name === '用户问题确诊能力') {
        this.answers = [[1], [3], [2], [0, 1, 2, 3], [3]]
      }
      if (this.$route.params.name === '学习总结能力') {
        this.answers = [
          [3],
          [2],
          [0],
          [0, 1, 3, 4],
          [0, 1, 2],
          [0, 1],
          [0, 1, 3],
          [1],
          [0, 1, 2, 3]
        ]
      }
      this.testAnswer = true
      this.$router.push('/study/ability-test-result/' + this.$route.params.name)
    }
  },
  beforeRouteLeave(to, from, next) {
    this.timer = null
    next()
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      to.meta.title = vm.$route.params.name
    })
  }
}
</script>
